import React from 'react';
import {Link} from 'react-router-dom';

function Header(){
    return(
        <div>
            <nav>
                <ul>
                    <li><Link to="/tabela">Tabela</Link></li>
                    <li><Link to="/cadastro">Cadastro de Posições</Link></li>
                    <li><Link to="/">Home</Link></li>
                </ul>
            </nav>
        </div>

    );

}

export default Header;